:root {
  // base
  --font-family: "DM Sans", sans-serif;
  --second-family: "Roboto", sans-serif;
  --content-width: 1252px;
  --container-offset: 30px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --bg-color: #ffed2c;
  --text-color: #0f1213;
  --border-color: #0f121333;
}
