.header {
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--border-color);
  padding: 16px 0;
  height: 93px;

  &__icon {
    height: 24px;

    &--social {
      width: 24px;
      height: 24px;
    }

    &--x {
      fill: transparent;
      stroke: var(--text-color);
      stroke-width: 2;
    }
  }

  &__list {
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 9px;

  &__span {
    display: block;
    width: 48px;
    height: 1px;
    background-color: var(--text-color);
  }
}
